@import url(https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@700&display=swap);
@font-face {
  font-family: 'Founders Grotesk';
  src: url(/dashboard/static/media/FoundersGrotesk-Regular.5e8e49c3.otf) format('otf'),
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Founders Grotesk', sans-serif;
}

.dashboard,
.analytics,
.balances,
.customers {
  display: -webkit-flex;
  display: flex;
  height: 90vh;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 2rem;
  margin-left: 280px;
  transition: 350ms;
}

.closed{
  margin-left: 0px;
  transition: 850ms;
}

.navbar {
  background-color: #fff;
  height: 80px;
  width: 280px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
}

.menu-bars {
  margin-left: 2rem;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  background: "none";
}

.nav-menu {
  background-color: #e5e5e5;
  width: 280px;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  position: fixed;
  top: 0;
  left: -280px;
  transition: 850ms;
}

.nav-menu.active {
  left: 0;
  transition: 350ms;
}

.nav-text {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
  padding: 8px 0px;
  list-style: none;
  height: 60px;
}

.nav-text a {
  text-decoration: none;
  color: #a6abb2;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  width: 100%;
  height: 52px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 0 40px;
}

.nav-text a:hover {
  background-color: #fff;
}

.selected {
  color: #000 !important;
  background-color: #fff;
}
.nav-menu-items {
  width: 280px;
}

.navbar-toggle {
  background-color: #e5e5e5;
  width: 280px;
  height: 80px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
}

span {
  margin-left: 16px;
}

.logo {
  padding: 40px;
}

.active {
  color: #000;
}

.menu-subheading {
  font-family: "Space Grotesk", "sans-serif";
  font-style: normal;
  font-weight: 600;
  font-size: 14px !important;
  line-height: 17px;
  color: #2a2a2a;
  padding: 15px 40px;
}

